import React from "react";
import { useLocalStore, useObserver } from "mobx-react";

import { observable, action } from "mobx";
import cobweb from "src/utils/cobweb";

import Table from "@material-ui/core/Table";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import { error } from "src/utils/tools";
import IButton from '../common/IButton';

class InvestListStore {
  @observable
  rows: any[] = [];

  @observable
  page = 0;
  @observable
  pageSize = 10;

  constructor() {
    this.init();
  }

  @action.bound
  async init() {
    try {
      const { cobWeb, web3 } = await cobweb();
      const data = await cobWeb.methods
        .getInvest(this.page * this.pageSize, (this.page + 1) * this.pageSize)
        .call();
      this.rows = [];
      data[0].map((item: any) => {
        const _wei = web3.utils.fromWei(item["amount"])
        return this.rows.push({
          amount: _wei,
          userAddress: item["userAddress"],
          withdraw: item["withdraw"],
          createTime: item["createTime"],
        });
      });
    } catch (error) {}
  }
  @action.bound
  async upper(){
    if(this.page === 0) return;
    this.page = 0;
    this.init();
  }
  @action.bound
  async next(){
    if(this.rows.length < this.pageSize) return ;
    this.page++;
    this.init();
  }
}

const store = new InvestListStore();

const InvestList: React.FC = () => {
  const iStore = useLocalStore(() => store);
  return useObserver(() => (
    <>
      <h3>入金列表</h3>
      <IButton onTap={iStore.upper}>上一页</IButton>
      <IButton onTap={iStore.next}>下一页</IButton>
      <Table aria-label="simple table">
        <TableHead>
          <TableRow>
            <TableCell>入金地址</TableCell>
            <TableCell>金额</TableCell>
            <TableCell>已提现</TableCell>
            <TableCell>入金时间</TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {iStore.rows.map((row:any,i:number) => (
            <TableRow key={i}>
              <TableCell component="th" scope="row">
                {row["userAddress"]}
              </TableCell>
              <TableCell>{row["amount"]}</TableCell>
              <TableCell>{row["withdraw"]}</TableCell>
              <TableCell>{row["createTime"]}</TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </>
  ));
};

export default InvestList;
